.nav-bar {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #283060;
    position: fixed;
    top: 0;
    left: 0;
    color: white;
    padding-top: 2.5vw;
    padding-bottom: 2vw;
    /* z-index: 1; */
}

.nav-items a {
    color: white;
    font-weight: 700;
    font-size: 1.1rem;
    text-decoration: none;
    padding: 0 2.5vw;
    transition: .3s;
    transition-property: color;
}

.nav-items a:hover {
    color: #ef224b;
}

.nav-list {
    list-style: none;
    padding-left: 0;
}

.logo {
    /* width: 200px; */
    height: 35px;
    margin-top: -8px;
}

.list-item {
    padding-bottom: 50px;
    /* margin-bottom: 500px; */
    /* padding: 500px 20px; */
}

.list-item a {
    color: white;
    text-decoration: none;
    font-weight: 700;
    font-size: 1.6rem;
    transition: .3s;
    transition-property: color;
}

.list-item a:hover {
    color: #ef224b;
}

.ham {
    position: relative;
    display: none;
    z-index: 9999;
    height: 40px;
    top: 20px;
}

.social {
    position: fixed;
    bottom: 0;
    left: 58px;
    display: flex;
    flex-direction: column;
    align-self: center;
    z-index: 2;
}

.social a {
    width: 100%;
    height: 100%;
    cursor: pointer;
    position: relative;
}

.social-i {
    color: white;
    font-size: 20px;
    margin: 10px auto;
    transition: .3s;
    transition-property: color;
}

.social-i:hover {
    color: #ef224b;
    -webkit-animation: wobble .75s 1 ease-in-out;
            animation: wobble .75s 1 ease-in-out;
}

.email {
    position: fixed;
    bottom: 0;
    right: 58px;
    display: flex;
    flex-direction: column;
    align-self: center;
    z-index: 999;
}

.add-on {
    -webkit-animation: bottomUp .7s 1 0s linear;
            animation: bottomUp .7s 1 0s linear;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}

.mail-line {
    background-color: white;
    width: 2px;
    height: 150px;
    margin-top: 30px;
}

.social-line {
    background-color: white;
    width: 2px;
    height: 135px;
    margin-top: 30px;
}

.mail {
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    font-size: 10px;
    line-height: 20px;
    text-decoration: none;
    left: 2px;
    color: white;
    cursor: pointer;
    position: relative;
    z-index: 999;
}

.mail:hover {
    color: #ef224b;
}

.floatingNav {
    height: 15px;
    box-shadow: 0 1px 10px #051020;
    padding-top: 1.5vw !important;
    transition: 350ms;
    transition-property: padding;
    z-index: 3;
}

.popup-overlay {
    display: none !important;
    position: relative;
    z-index: 99999;
}

.popout {
    z-index: 99999;
    position: relative;
}

@media (max-width: 1300px) {
    .add-on {
        display: none;
    }
}

@media (max-width: 990px) {
    .ham {
        display: block;
        margin-left: 50%;
    }
    .nav-items {
        display: none;
    }
    .nav-bar {
        padding-top: 4vw; 
    }
    .popup-overlay {
        display: flex !important;
    }
    .floatingNav {
        height: 30px;
        padding-top: 1.5vw !important;
    }
    .logo {
        margin-top: 0px;
    }
}

@media (max-width: 848px) {
    .floatingNav {
        height: 35px;
    }
}

@media (max-width: 600px) {
    .floatingNav {
        height: 40px;
    }
}

@media (max-width: 400px) {
    .logo {
        width: 100px;
        height: 30px
    }
}

@media (max-width: 320px) {
    .logo {
        width: 90px;
        height: 25px
    }
}

@-webkit-keyframes bottomUp {
    0% {
        transform: translateY(150px);
        opacity: 0;
    }
    100% {
        transform: rotate(xx) translateY(0);
        opacity: 1;
    }
}

@keyframes bottomUp {
    0% {
        transform: translateY(150px);
        opacity: 0;
    }
    100% {
        transform: rotate(xx) translateY(0);
        opacity: 1;
    }
}

@-webkit-keyframes wobble {
    25% {
      transform: translateY(-8px);
    }
    50% {
      transform: translateY(0px);
    }
    75% {
      transform: translateY(-4px);
    }
    100% {
      transform: translateY(0px);
    }
}

@keyframes wobble {
    25% {
      transform: translateY(-8px);
    }
    50% {
      transform: translateY(0px);
    }
    75% {
      transform: translateY(-4px);
    }
    100% {
      transform: translateY(0px);
    }
}

.hamburger-center {
    content: "";
    height: 4px;
    width: 50px;
    background: white;
    border-radius: 4px;
    position: relative;
    -webkit-animation: rotateCenter0Deg 1s both ease-in-out;
            animation: rotateCenter0Deg 1s both ease-in-out;
}

.hamburger-top {
    content: "";
    display: block;
    height: 4px;
    background: white;
    border-radius: 4px;
    position: absolute;
    top: -10px;
    left: 0;
    width: 25px;
    -webkit-animation: rotateTop0Deg 1s both ease-in-out;
            animation: rotateTop0Deg 1s both ease-in-out;
}

.hamburger-bottom {
    content: "";
    display: block;
    height: 4px;
    background: white;
    border-radius: 4px;
    position: absolute;
    top: 10px;
    right: 0;
    width: 25px;
    -webkit-animation: rotateBottom0Deg 1s both ease-in-out;
            animation: rotateBottom0Deg 1s both ease-in-out;
}

.hamburger-center.active {
    -webkit-animation: rotateCenter360Deg 1s both ease-in-out;
            animation: rotateCenter360Deg 1s both ease-in-out;
}

.hamburger-top.active {
    -webkit-animation: rotateTop45Deg 1s both ease-in-out;
            animation: rotateTop45Deg 1s both ease-in-out;
}

.hamburger-bottom.active {
    -webkit-animation: rotateBottom45Deg 1s both ease-in-out;
            animation: rotateBottom45Deg 1s both ease-in-out;
}

.hamburger-center.loading {
    -webkit-animation: rotateCenter360Deg 1s alternate infinite ease-in-out;
            animation: rotateCenter360Deg 1s alternate infinite ease-in-out;
}

.hamburger-top.loading {
    -webkit-animation: rotateTop45Deg 1s alternate infinite ease-in-out;
            animation: rotateTop45Deg 1s alternate infinite ease-in-out;
}

.hamburger-bottom.loading {
    -webkit-animation: rotateBottom45Deg 1s alternate infinite ease-in-out;
            animation: rotateBottom45Deg 1s alternate infinite ease-in-out;
}

@-webkit-keyframes rotateCenter0Deg {
from {
    transform: rotate(315deg);
}
to {
    transform: rotate(0deg);
}
}

@keyframes rotateCenter0Deg {
from {
    transform: rotate(315deg);
}
to {
    transform: rotate(0deg);
}
}

@-webkit-keyframes rotateTop0Deg {
from {
    left: 5px;
    transform: rotate(45deg);
}
to {
    left: 0;
    transform: rotate(0deg);
}
}

@keyframes rotateTop0Deg {
from {
    left: 5px;
    transform: rotate(45deg);
}
to {
    left: 0;
    transform: rotate(0deg);
}
}

@-webkit-keyframes rotateBottom0Deg {
from {
    top: 8px;
    right: 2px;
    transform: rotate(45deg);
}
to {
    top: 10px;
    right: 0;
    transform: rotate(0deg);
}
}

@keyframes rotateBottom0Deg {
from {
    top: 8px;
    right: 2px;
    transform: rotate(45deg);
}
to {
    top: 10px;
    right: 0;
    transform: rotate(0deg);
}
}

@-webkit-keyframes rotateTop45Deg {
from {
    transform: rotate(0deg);
}
to {
    left: 5px;
    transform: rotate(45deg);
}
}

@keyframes rotateTop45Deg {
from {
    transform: rotate(0deg);
}
to {
    left: 5px;
    transform: rotate(45deg);
}
}

@-webkit-keyframes rotateBottom45Deg {
from {
    transform: rotate(0deg);
}
to {
    top: 8px;
    right: 2px;
    transform: rotate(45deg);
}
}

@keyframes rotateBottom45Deg {
from {
    transform: rotate(0deg);
}
to {
    top: 8px;
    right: 2px;
    transform: rotate(45deg);
}
}

@-webkit-keyframes rotateCenter360Deg {
from {
    transform: rotate(0deg);
}
to {
    transform: rotate(315deg);
}
}

@keyframes rotateCenter360Deg {
from {
    transform: rotate(0deg);
}
to {
    transform: rotate(315deg);
}
}
